import React, { useState, useRef } from 'react';
import styled from 'styled-components';
import PopupChannels from '../shared/popup-channels';
import Popup from '../shared/popup';
import { Link } from 'gatsby';
import packageBottomImg from '../../images/packages/package-bottom.svg'

const PackageWrapper = styled.div`
  transform: scale(${props => props.isScaledDown ? 0.95 : 1});
  box-shadow: 0px 0px 20px rgba(17, 6, 29, 0.2);
  border-radius: 8px;
  margin-bottom: 3rem;
`;

const PackageCard = styled.div`
  background: #ffffff;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  min-width: 354px;
  max-width: 355px;
  @media screen and (max-width: 400px) {
    min-width: 200px;
    max-width: 250px;
  }
`;

const PackageCardBottom = styled.div`
  width: 100%;
  padding: 0;
  margin: 0;
  background-image: url(${packageBottomImg});
  background-position: 100% 100%;
  background-repeat: no-repeat;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  background-color: rgba(17, 6, 29, 0.1);
`;

const PackageHeader = styled.div`
  min-height: 9rem;
`;

const Package = ({ packageInfo, isScaledDown, extend, setExtend }) => {
  const [showChannels, setShowChannels] = useState(false);
  const [showInfo, setShowInfo] = useState(false);
  const infoIconRef = useRef(null);

  const isFull = !packageInfo.isOnlyInternet;

  return (
    <>
      <PackageWrapper isScaledDown={isScaledDown}>
        <div className='package'>
          <PackageCard className='py-2 col-12'>
            <div className='row d-flex justify-content-center align-items-center'>
              <PackageHeader className='text-center d-flex justify-content-center flex-column'>
                <img src={packageInfo.icon} alt='Икона на пакета' />
                <h3 className='my-3'>{packageInfo.internalTitle}</h3>
              </PackageHeader>
            </div>

            <div className='row py-sm-3 b-bottom mx-sm-2'>
              <div className={isFull ? 'col-12 col-sm-6 text-center b-right' : 'col-12 text-center'}><p>Интернет<br /><span className='text-blue'>{packageInfo.internetSpeed}</span></p></div>
              <div className={isFull ? 'col-12 col-sm-6 d-none d-sm-block text-center' : 'd-none'}><p>Телевизия<br /><span className='text-blue'>{packageInfo.tvChannels} канала</span></p></div>
            </div>

            <div className='row pt-3 b-bottom mx-2 d-flex justify-content-center align-items-center'>
              <div className='col-6 row p-0'>
                <div className='col-6 p-0 d-flex justify-content-end align-items-center'>
                  <h3 className='price'><span className='text-blue'>{packageInfo.getPriceMain()}</span></h3>
                </div>
                <div className='col-6 p-0 m-0 row d-flex justify-content-start '>
                  <h3 className='price-coins'><span className='text-blue'>{packageInfo.getPriceCoins()}</span></h3>
                  <h3 className='price-label pb-1' style={{whiteSpace: 'nowrap'}}><span className='text-blue'>лв/мес</span></h3>
                </div>
              </div>
            </div>

            <div className='row py-4 b-bottom mx-2 d-none d-lg-flex justify-content-center align-items-center'>
              <p>Подходящ за:</p>
              <div className='row d-inline-block d-flex justify-content-center align-items-center w-100'>
                {packageInfo.icons.map((icon, index) => (
                  <div key={index} className='mx-3 d-inline'>
                    <img src={icon} alt='Пакета е подходящ' />
                  </div>
                ))}
              </div>
            </div>

            <div className='row py-2 b-bottom mx-2'>
              {isFull && (
                <div className='row w-100 b-bottom-faded d-none d-lg-flex justify-content-between align-items-center'>
                  <p className='m-1'>Цифрови канала</p>
                  <p className='m-1'>
                    <span className='text-blue'>{packageInfo.tvChannels}</span>
                    <button className='clear ml-1 p-0' onClick={() => setShowChannels(true)}>
                      <span className='icon package view-channels'></span>
                    </button>
                  </p>
                </div>
              )}

              <div className={'row w-100 d-none d-lg-flex justify-content-between align-items-center ' + (isFull && 'b-bottom-faded')} >
                <p className='m-1'>Интернет скорост</p>
                <p className='m-1'><span className='text-blue'>{packageInfo.internetSpeed}</span></p>
              </div>

              {isFull && (
                <>
                  <div className='row w-100 b-bottom-faded d-flex justify-content-between align-items-center'>
                    <p className='m-1'>HD канала</p>
                    <p className='m-1'><span className='text-blue'>{packageInfo.hdChannels}</span></p>
                  </div>
                  <div className='row w-100 d-flex justify-content-between align-items-center'
                    ref={infoIconRef}
                  >
                    <p className='m-1'>IPTV приемник <span className='d-none d-sm-inline'>(STB)</span><span
                        onMouseEnter={(e) => setShowInfo(true)}
                        onMouseLeave={(e) => setShowInfo(false)}
                        className='icon info ml-1'>
                      </span>
                    </p>
                    <p className='m-1'><span className='text-blue'>{packageInfo.hdFetcher}</span></p>
                  </div>
                </>
              )}
            </div>

            {!extend &&
              (<ChoosePackage packageInfo={packageInfo} />)}

            {extend && (
              <div className='row pt-4 d-flex justify-content-center align-items-center'>
                <div className='row p-0 m-0 w-100'>
                  <ul className='package'>
                    {packageInfo.packageFeatures.map(({ text, active }, index) => (
                      <li key={index + text} className={'p-0 m-0 ' + (active ? 'active-feature' : 'non-active-feature')}>
                        <p className='p-1 m-0 text-left'>{text}</p>
                      </li>
                    ))}
                  </ul>
                </div>

                <ChoosePackage packageInfo={packageInfo} />
              </div>
            )}

          </PackageCard>
          <PackageCardBottom className='col-12 m-0 p-0 py-1 text-center'>
            <button
              className='clear link'
              onClick={(_) => { setExtend(!extend) }}
            >
              {extend ? 'Скрий' : 'Покажи'}
            </button>
          </PackageCardBottom>
        </div>
      </PackageWrapper>

      <Popup
        showTelephonePopover={showInfo}
        target={infoIconRef}
        placement='top-end'
      >
        <div
          onMouseLeave={(_) => setShowInfo(false)}
        >
          <div className='phone-pop-up bg-white col-12 col-md-3 px-5 py-3 mx-5' style={{maxWidth: '350px'}}>
            <p>{packageInfo.hdFetcherInfo}</p>
          </div>
        </div>
      </Popup>

      <PopupChannels
        show={showChannels}
        setShow={(v) => { setShowChannels(v) }}
      />
    </>
  )
}

const ChoosePackage = ({ packageInfo }) => (
  <Link
    to={'/checkout'}
    state={{ selectedPackageId: packageInfo.id, packageTvCount: packageInfo.hdFetcher }}
    className={'my-4 btn ' + (packageInfo.isPrimaryPackage ? 'btn-primary green' : 'btn-secondary-no-outline')}
  >Избери пакет</Link>
)

export default Package;