import React, { useState } from 'react';
import Package from './package';
import Slider from 'react-slick';

const PackageSection = ({ combinedPackages, internetPackages, enableToggle }) => {
  const [isOn, setToggle] = useState(true);
  const [extendPackages, setExtendPackages] = useState(false);

  return (
    <>
      <div className='row d-flex justify-content-center text-center'>
        {enableToggle && (
          <Toggle
            isOn={isOn}
            setOn={setToggle}
            leftText={'ТВ + Интернет'}
            leftShortText={'ТВ + Нет'}
            rightText={'Интернет'}
            rightShortText={'Нет'}
          />
        )}
        <div className='row d-flex justify-content-center reasons-slider w-100'>
          <RenderPackages
            packageList={isOn ? combinedPackages : internetPackages}
            extend={extendPackages}
            setExtend={(v) => setExtendPackages(v)}
          />
        </div>
      </div>
    </>
  )
}

const RenderPackages = ({ extend, setExtend, packageList }) => (
  <>
    <div className='d-block w-100 d-lg-none packages-list'>
      <Slider {...settings}>
        {packageList.map((p, index) => (
          <div key={index} className={'col-12 d-flex justify-content-center mt-5'}>
            <Package
              isScaledDown={index !== 1}
              packageInfo={p}
              extend={extend}
              setExtend={setExtend}
            />
          </div>
        ))}
      </Slider>
    </div>
    <div className='d-none d-lg-block container'>
      <div className='row'>
        {packageList.map((p, index) => (
          <div key={index} className={'col-4 d-flex justify-content-center mt-5 px-2'}>
            <Package
              isScaledDown={index !== 1}
              packageInfo={p}
              extend={extend}
              setExtend={setExtend}
            />
          </div>
        ))}
      </div>
    </div>
  </>
)

export const Toggle = ({ isOn, setOn, leftText, leftShortText, rightText, rightShortText }) => {
  return (
    <>
      <div className='d-inline-block toggle-wrapper' style={{ whiteSpace: 'nowrap' }}>
        <span className={'toggle ' + (isOn && 'active overlap')}>
          <button className='clear' onClick={(_) => setOn(true)}>
            <h3 className='d-inline d-sm-none'>{leftShortText}</h3>
            <h3 className='d-none d-sm-inline'>{leftText}</h3>
          </button>
        </span>
        <span className={'toggle ' + (!isOn && 'active overlap left')}>
          <button className='clear' onClick={(_) => setOn(false)}>
            <h3 className='d-inline d-sm-none'>{rightShortText}</h3>
            <h3 className='d-none d-sm-inline'>{rightText}</h3>
          </button>
        </span>
      </div>
    </>
  )
}

const settings = {
  dots: false,
  speed: 1000,
  arrows: false,
  infinite: false,
  initialSlide: 1,
  slidesToShow: 1,
  slidesToScroll: 1,
  centerMode: true,
  centerPadding: '250px',
  focusOnSelect: true,
  pauseOnFocus: true,
  dots: true,
  responsive: [
    {
      breakpoint: 860,
      settings: {
        centerPadding: '200px',
      }
    },
    {
      breakpoint: 760,
      settings: {
        centerPadding: '150px',
      }
    },
    {
      breakpoint: 610,
      settings: {
        centerPadding: '100px',
      }
    },
    {
      breakpoint: 500,
      settings: {
        centerPadding: '50px',
      }
    },
    {
      breakpoint: 400,
      settings: {
        centerPadding: '50px',
      }
    },
    {
      breakpoint: 360,
      settings: {
        centerPadding: '30px',
      }
    },
  ]
}

export default PackageSection;